@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#757083}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}html{scroll-behavior:smooth}

body{font-family:'Nunito', sans-serif}h1{font-family:'Josefin Sans', cursive;font-weight:800 !important}.navbar-ex{top:0;position:fixed;display:flex;background-color:#0a0a0a;width:100%;box-shadow:4px 4px 8px 1px #686686}.scrollspy{list-style-type:none;display:flex;justify-content:space-evenly;margin-bottom:0}.scrollspy li{font-size:20px;padding:1rem !important;border-bottom:0.3rem solid #0a0a0a}.scrollspy li a{text-decoration:none;color:#aaa !important}.scrollspy .active{font-size:20px;border-bottom:0.3rem solid #fff}.scrollspy .active a{color:#fff !important}.scroll-spy{background-color:#ECEEF1}.scroll-spy .tab{background-color:#fff;padding-top:90px;box-shadow:0px 3px 5px 3px rgba(0,0,0,0.25);margin-bottom:20px}

.tab-work-exp{background:url(/portfolio/static/media/home.0c749b6d.jpg) no-repeat center center fixed;background-size:cover}.tab-work-exp .intro{display:flex;flex-direction:column;align-items:center;padding-top:5vh}.tab-work-exp .intro h1{border-bottom:3px solid #0a0a0a}.tab-work-exp .intro img{height:200px;box-sizing:border-box;border-radius:110.5px;margin:10px}

.tab-projects{padding:20px}.tab-projects-list{display:grid;grid-gap:75px;padding:50px 150px;grid-template-columns:1fr 1fr}.tab-projects-list-item{border:1px solid #E3E3E3;cursor:pointer;box-sizing:border-box;box-shadow:2px 2px 10px 2px #8E8C8C;border-radius:40px;display:flex;flex-direction:column;align-items:center}.tab-projects-list-item:hover{box-shadow:4px 4px 25px 2px #8E8C8C}.tab-projects-list-item p{text-align:center}

.tab-experience{padding:20px}.tab-experience-list{display:flex;padding:50px;justify-content:space-around}.tab-experience-list-item{width:450px;border:1px solid #E3E3E3;box-sizing:border-box;box-shadow:2px 2px 10px 2px #8E8C8C;border-radius:40px;display:flex;flex-direction:column;align-items:center}.tab-experience-list-item:hover{box-shadow:4px 4px 25px 2px #8E8C8C}.tab-experience-list-item p{text-align:center}

.tab-skills{padding:20px}.tab-skills-list{padding:50px;justify-content:space-around}.tab-skills-list-item{display:flex;justify-content:space-between;padding:10px 100px}.tab-skills-list-item-outer{box-shadow:0px 2px 3px 2px rgba(110,110,110,0.25);height:22px;width:800px;border-radius:10px}.tab-skills-list-item-inner{width:720px;height:22px;border-radius:10px;opacity:0.7}.tab-skills-list-item-inner span{color:#fff;font-weight:600;padding-left:10px;font-size:12px}

.tab-contacts{padding:20px}.tab-contacts-list{padding:0 100px;display:grid;grid-template-columns:1fr 1fr}.tab-contacts a{display:flex;align-items:center;text-decoration:none;color:#777;text-decoration:none !important}.tab-contacts a span{padding-left:10px}.tab-contacts .icon{cursor:pointer;padding:10px 20px;width:100px;height:100px}.tab-contacts .icon:hover{border-radius:50px}.tab-contacts .git{color:#3d2d00}.tab-contacts .git:hover{background-color:#3d2d00;color:#fff}.tab-contacts .ln{color:#0e76a8}.tab-contacts .ln:hover{background-color:#0e76a8;color:#fff}.tab-contacts .mail{color:#bb001b}.tab-contacts .mail:hover{background-color:#bb001b;color:#fff}.tab-contacts .phone{color:#0e76a8}.tab-contacts .phone:hover{background-color:#0e76a8;color:#fff}.tab-contacts .insta{color:#dd2a7b}.tab-contacts .insta:hover{background-color:#dd2a7b;color:#fff}

.tab-education{padding:20px}.tab-education-list{display:flex;padding:50px;justify-content:space-around}.tab-education-list-item{width:450px;border:1px solid #E3E3E3;box-sizing:border-box;box-shadow:2px 2px 10px 2px #8E8C8C;border-radius:40px;display:flex;flex-direction:column;align-items:center}.tab-education-list-item:hover{box-shadow:4px 4px 25px 2px #8E8C8C}.tab-education-list-item p{text-align:center}

